@import "helpers/reset";
@import "helpers/mixins";

$black: #000000;
$white: #ffffff;
$lightGrey: #cccccc;
$badBlue: #4f8a8b;
$yellow: #fbd46d;
$orange: #e65c00;
$red: #FF0043;
$crimson: crimson;
$transition: all 0.3s ease;

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: Raleway;
  font-weight: 500;
  letter-spacing: 1.15px;
  color: $black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  scroll-behavior: smooth;
  overflow: hidden; }

main {
  position: relative;
  min-height: 100vh;
  width: 100%; }

$section: section;

$colors: red, black, orange, green, rebeccapurple, yellow;


svg {
  width: 100%;
  height: 100%; }

a {
  text-decoration: none;
  transition: $transition;
  color: $black;
  &:hover,
  &:focus {
    color: $white; } }

picture {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

p {
  line-height: 2rem; }

.header {
  display: flex;
  width: 100%;
  min-height: 3.5rem;
  background-color: #FF0043;
  z-index: 999;
  position: fixed; }

.navigation {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;

  &__wrapper {
    display: flex;
    justify-self: flex-end;
    justify-content: space-between;
    width: 50%;
    align-items: center; }

  &__item {
    width: 100%;
    height: 100%;
    text-align: center; }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    width: 100%;
    padding: 1.5rem 1rem;
    font-size: 1.25rem;
    color: $lightGrey;

    &:after {
      content: '';
      display: block;
      width: 70%;
      height: 2px;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      transform: translateX(20%);
      background-color: $orange;
      visibility: hidden;
      opacity: 0; }

    &:hover,
    &:focus,
    &:active {
      color: $black;
      &:after {
        visibility: visible;
        opacity: 1; } } } }

.logo {
  width: 15%;
  padding: 0.25rem 0;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  svg {
    height: 3.5rem; } }

.section {
  display: flex;
  padding: 12.5rem 20rem;
  align-items: center;
  text-align: center;
  width: 100vw;
  min-height: 100vh;
  position: absolute;
  font-size: 6rem;
  transform-origin: left center;

  &--active {
    z-index: 10;
    background-color: rgba($orange, 0.35); } }

.spinner {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;

  &--wrapper {
    border: 2px dashed $black;
    position: absolute;
    height: 50vh;
    width: 25%;
    left: 0;
    transform: translateX(-50%);
    margin: auto 0;
    border-radius: 50%; } }


.footer {
  position: fixed;
  width: 100%;
  min-height: 3.5rem;
  bottom: 0;
  left: 0;
  z-index: 10;
  background-color: rgba($black, 0.25);
  display: flex;
  align-items: center;
  justify-content: space-between;
  &--wrapper {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center; } }

.social-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  margin-right: auto;

  &__item {
    width: 100%; }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center; } }

.rotate {
  transform: translateX(20px); }

@import "helpers/fonts";
